// Import all images as Vite assets
import abhaLogo from '../../assets/images/abha-logo.jpg';
import communityHero from '../../assets/images/community-hero.jpg';
import heroPoster from '../../assets/images/community-hero.jpg'; // Using community-hero as hero poster
import sheuliMagazine from '../../assets/images/sheuli-magazine-2023.png';

// Video assets
import heroVideo from '../../assets/video/bengali-culture-hero.mp4';

// Event images
import durgaPuja from '../../assets/images/events/durga-puja.jpg';
import saraswatiPuja from '../../assets/images/events/saraswati-puja.jpg';
import upcomingEvent from '../../assets/images/events/upcoming-event-poster.jpg';

/**
 * Asset map for easy access to imported images
 */
export const assets: Record<string, string> = {
  'assets/images/abha-logo.jpg': abhaLogo,
  'assets/images/community-hero.jpg': communityHero,
  'assets/images/hero-poster.jpg': heroPoster,
  'assets/images/sheuli-magazine-2023.png': sheuliMagazine,
  'assets/images/events/durga-puja.jpg': durgaPuja,
  'assets/images/events/saraswati-puja.jpg': saraswatiPuja,
  'assets/images/events/upcoming-event-poster.jpg': upcomingEvent,
  // Create placeholders for missing images using existing ones
  'assets/images/events/summer-picnic.jpg': durgaPuja,
  'assets/images/events/Boishakhi.jpg': saraswatiPuja,
  'assets/images/events/durga-puja-2024.jpg': durgaPuja,
  'assets/images/events/summer-picnic-2024.jpg': saraswatiPuja,
  'assets/images/events/Boishakhi-2024.jpg': upcomingEvent,
  'assets/images/events/saraswati-puja-2024.jpg': saraswatiPuja,
  // Short aliases
  'abhaLogo': abhaLogo,
  'communityHero': communityHero,
  'heroPoster': heroPoster,
  'heroVideo': heroVideo,
  'sheuliMagazine': sheuliMagazine,
  'durgaPuja': durgaPuja,
  'saraswatiPuja': saraswatiPuja,
  'upcomingEvent': upcomingEvent,
};

/**
 * Get proper asset URL using Vite's import system
 */
export const getAssetUrl = (path: string): string => {
  // Remove leading slash if present
  const cleanPath = path.startsWith('/') ? path.slice(1) : path;
  
  // Return imported asset URL or fallback
  return assets[cleanPath] || cleanPath;
};
